.footer {
  padding-top: 100px;
  padding-bottom: 70px;
  .container {
    .wrapper {
      max-width: 70%;
      margin: 0px auto;
      .img-container {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 30px;
      }
      .global-menu {
        display: flex;
        justify-content: center;
        margin-bottom: 30px;
        button, img {
          margin-right: 25px;
        }
      }
      .footer__descr {
        font-family: Atyp Display;
        font-size: 16px;
        font-weight: 400;
        line-height: 24px;
        letter-spacing: 0em;
        text-align: left;
        color: $blue;
        span {
          font-weight: 700;
        }
      }
      .footer__link-developer {
        color: $blue;
        font-family: Atyp Display;
        font-size: 16px;
        font-weight: 700;
        line-height: 24px;
        letter-spacing: 0em;
        text-align: left;
      }
    }
  }
}