h2 {
    vertical-align: center;
    text-align: center;
  }
  
  html, body {
    margin: 0;
    height: 100%;
  }
  
  * {
    font-family: "Raleway";
    box-sizing: border-box;
  }
  
  .top-nav {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    color: white;
    padding: 50px 0px;
    height: 200px;
  }

  .header-link {
    display: flex;
    align-items: center;
    font-size: 16px;
    color: $red;
    .link__title-japan {
      font-family: Atyp Display;
      font-weight: 400;
      letter-spacing: 0em;
      text-align: left;
    }
    font-size: 32px;
    img {
      margin-left: 20px;
    }
    .line {
      margin-left: 20px;
      width: 15px;
      background: $blue;
      height: 3px;
    }
  }
 
  
  .header__text {
    color: $blue;
    font-family: Atyp Display;
    font-weight: 700;
    letter-spacing: 0em;
    text-align: center;
  }
  
  .menu__link-btn {
    display: block;
    background: $blue;
    color: white  ;
    padding: 15px 35px;
    border-radius: 25px
  }
  
  .menu-button-container {
    display: none;
    height: 100%;
    width: 30px;
    cursor: pointer;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  
  #menu-toggle {
    display: none;
  }
  
  .menu-button,
  .menu-button::before,
  .menu-button::after {
    display: block;
    background-color: black;
    position: absolute;
    height: 4px;
    width: 30px;
    transition: transform 400ms cubic-bezier(0.23, 1, 0.32, 1);
    border-radius: 2px;
  }
  
  .menu-button::before {
    content: '';
    margin-top: -8px;
  }
  
  .menu-button::after {
    content: '';
    margin-top: 8px;
  }
  
  #menu-toggle:checked + .menu-button-container .menu-button::before {
    margin-top: 0px;
    transform: rotate(405deg);
  }
  
  #menu-toggle:checked + .menu-button-container .menu-button {
    background: rgba(255, 255, 255, 0);
  }
  
  #menu-toggle:checked + .menu-button-container .menu-button::after {
    margin-top: 0px;
    transform: rotate(-405deg);
  }
  
 
