$blue: #0f0f50;
$lila: #beaaff;
$yellow: #ffcd2d;
$red: #E52322;
$grey: #333333;


// базовые подключения
@import "vars";
@import "mixins";
@import "fonts";
@import "settings";

// подключения компонентов страницы
@import "./components/header";
@import "./components/footer";


.section__descr {
  font-family: Atyp Display;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  letter-spacing: 0em;
  text-align: left;
  span {
    font-size: 130%;
  }
}
.section__title {
  font-family: Atyp Display;
  font-size: 67px;
  font-weight: 700;
  line-height: 76px;
  letter-spacing: 0em;
  text-align: center;
}


.container { 
  max-width: 1440px;
  margin: 0px auto;
}

.btn {
  border: 2px solid $lila;
  padding: 15px 60px;
  border-radius: 30px;
  background: none;
  .btn__link {
    font-family: Atyp Display;
    font-size: 20px;
    font-weight: 400;
    line-height: 22px;
    letter-spacing: 0em;
    text-align: left;
    color: $blue;
  }
}

.btn-yellow {
  border: 2px solid $yellow;
  .btn__link-yellow {
    color: $yellow;
  }
}

.btn-blue {
  background: $blue;
  border: none;
  .btn__link {
    color: $lila;
  }
}

.greeting {
  padding-top: 30px;
  padding-bottom: 170px;
  .container {
    display: flex;
    justify-content: space-between;
    align-items: end;
    .section__text {
      .section__pretitle {
        font-family: Atyp Display;
        font-size: 16px;
        font-weight: 400;
        line-height: 24px;
        letter-spacing: 0em;
        text-align: left;
        color: $lila;    
        margin-bottom: 20px;
      }
      img {
        margin-bottom: 15px;
      }
      .section__descr {
        color: $blue;
        margin-bottom: 25px;
      }
      .number-telephone {
        display: block;
        margin-top: 20px; 
        font-family: Atyp Display;
        font-size: 20px;
        font-weight: 700;
        line-height: 21px;
        letter-spacing: 0em;
        color: $blue; 
        border: 1px solid $blue;
      }
    }
  }
}

