.app {
  padding-top: 60px;
  padding-bottom: 75px;
  .container {
    .wrapper {
      width: 70%;
      margin: 0px auto;
      display: flex;
    justify-content: space-between;
    align-items: center;
    .app-text {
      .section__title {
        text-align: left;
        margin-bottom: 20px;
        color: $blue;
      }
      .section__descr {
        color: $blue;
        margin-bottom: 20px;
        span {
          font-weight: 700;
        }
      }
      .app-container {
        .app-link {
          display: block;
          margin-bottom: 15px;
        }
      }
    }
    }
  }
}

.group {
  background: $blue;
  padding-top: 90px;
  padding-bottom: 90px;
  .container {
    .wrapper {
      width: 90%;
      margin: 0px auto;
      display: flex;
      align-items: center;
      justify-content: space-between;
      .group-text {
        .section__title {
          color: $yellow;
          text-align: left;
          margin-bottom: 15px;
        }
        .section__descr {
          color: $yellow;
          margin-bottom: 25px;
        }
      }
    }
  }
}


.shop {
  padding-top: 90px;
  padding-bottom: 90px;
  .container {
    .wrapper {
      width: 80%;
      margin: 0px auto;
      display: flex;
      align-items: center;
      justify-content: space-between;
      .shop-text {
        .section__title {
          text-align: left;
          color: $blue;
        }
        .section__descr {
          color: $blue;
          margin-bottom: 25px;
        }
      }
    }
  }
}


@media screen and (max-width: 1440px) {
  .container {
  width: 1200px;
  }
  }
  
  @media screen and (max-width: 1199px) {
  .container {
  width: 960px;
  }
  .greeting {
    .container {
      .greeting-img {
        transform: scale(0.6);
      }
    }
  }
  .info-burger, .group-img, .app-img, .about-img {
    transform: scale(0.6);
  }
  .wrapper {
    width: 100%;
  }
  .info {
    .info-container {
      display: grid;
      grid-template-columns: auto auto;
    }
  }
  .delivery {
    .container {
      .delivery__content {
        flex-direction: column;
      }
    }
  }
  .app {
    .container {
      .wrapper {
        flex-direction: column;
        .app-text {
          .section__title, .section__descr, .app-container {
            text-align: center;
          }
        }
      }
    }
  }
  .shop {
    .container {
      .wrapper {
        flex-direction: column;
        img {
          transform: scale(0.7);
        }
      }
    }
  }
  .footer {
    .container {
      .img-container {
        display: flex;
        flex-direction: column;
        img {
          margin-bottom: 25px;
        }
      }
      .btns {
        display: flex;
        flex-direction: column;
        .btn {
          margin-bottom: 25px;
        }
      }
      .app-container {
        flex-direction: column;
        align-items: center;
        a {
          margin-bottom: 25px; 
          
        }
      }
    }
  }
  }
  
  @media screen and (max-width: 991px) {
  .container {
  width: 720px;
  }
  .top-nav {
    display: block;
    margin-bottom: 50px;
  }
  .header-link-wrapper {
    display: flex;
    justify-content: center;
    .header-link {
      text-align: center;
    }
  }
  .greeting {
    padding-bottom: 0;
    .container {
      flex-direction: column;
      .section__text {
        margin: 0px auto;
      }
    }
  }
  .group {
    .container {
      .wrapper {
        flex-direction: column-reverse;
        .group-text {
          .section__title, .section__descr {
            text-align: center;
          }
          .btn {
            display: block;
            margin: 0px auto;
          }
        }
      }
    }
  }
  }
  
  @media screen and (max-width: 767px) {
  .container {
  width: 540px;
  }
  
  .about {
    .container {
      .wrapper {
        flex-direction: column;
        .section__text {
          .section__title, .section__descr {
            text-align: center;
          }
          .btn {
            display: block;
            margin: 0px auto;
          }
        }
      }
    }
  }
  }
  
  @media screen and (max-width: 575px) {
  .container {
  width: 100%;
  padding: 0 15px;
  }
  .header-link {
    display: block;
    img {
      display: block;
      margin: 0px auto;
    }
    .link__title-japan {
      display: none;
    }
  }
  }